.App {
  text-align: center;
  padding: 0 30px;
  background-color: #f4f6ff;
}
@import url('https://fonts.googleapis.com/css2?family=Lato:ital@1&display=swap');

.gallery{

  padding: 20px;
  background-color:#f7f7f7;
}

.cards{
  display: flex;
  align-items: center;
  flex-direction: column;
}

.card_box{
  display: flex;
}

.card{
  display: flex;
  flex-direction: column;
  justify-content: space-between;
  align-items: center;
  padding: 10px 20px;
  background-color: #ddf3f5;
  margin : 30px 0;
  width: 80%;
  box-shadow: 0 15px 40px rgba(0,0,0,0.2);
}

.card_image{
  width: 30%;
  border : 10px solid black;
  flex:40%;
  align-self:center;
  transition: all .2s;
}

.card_image:hover{
  transform: scale(1.25);
}

.card_heading{
  font-size: 26;
  font-family: 'Oswald', sans-serif;
  letter-spacing: 5px;
  text-transform: uppercase;
  color: #3c2c3e;
}
.card_detail{
  margin: 0 5px;
  flex:50%;
  font-family: 'Lato', sans-serif;
  font-size: 24px;
  padding: 0 60px;
}

.header{
  text-align: center;
  background-color:#0f4c75;
  width: 80%;
  margin: auto;
}
.heading{
  text-transform: uppercase;
  font-size: 50px;
  letter-spacing: 10px;
  color: #FFF;
  
}
@import url('https://fonts.googleapis.com/css2?family=Oswald&display=swap');
